* {
  font-family: "Courier New", Courier, monospace;
}

.home-screen {
  position: absolute;
  width: 100%;
  height: 100%;
  background: rgb(2, 0, 36);
  background: linear-gradient(
    353deg,
    rgba(2, 0, 36, 1) 0%,
    rgba(9, 9, 121, 1) 35%,
    rgba(0, 97, 117, 1) 100%
  );
  padding: 20px;
  box-sizing: border-box;
}

.title {
  text-align: center;
  color: white;
}

.graph-container {
  width: 300px;
  height: 300px;
  background-color: rgba(0, 0, 255, 0.2);
  border-radius: 20px;
  border: 2px solid blue;
  cursor: pointer;
  transition: all 0.5s ease;
  padding: 20px;
  box-sizing: border-box;
}

.graph-container:hover {
  transform: scale(1.1);
}

.item-label {
  color: white;
}

.item-icon {
  width: 20px;
  height: 20px;
}
